/* Common */

body {
  --bg-opacity: 1;
  background-color: #718096;
  background-color: rgba(113, 128, 150, var(--bg-opacity));
}

.button-icon {
  margin: auto;
  width: auto;
  height: 75%;
}

.button-with-icon {
  border-radius: 0.5rem;
  width: 3rem;
  height: 3rem;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  white-space: nowrap;
}

.button-with-icon img {
  margin: auto;
  width: auto;
  height: 75%;
}

.button-with-text {
  border-radius: 0.5rem;
  width: 3rem;
  height: 3rem;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  white-space: nowrap;
  width: auto;
}

.button-with-icon-text {
  border-radius: 0.5rem;
  width: 3rem;
  height: 3rem;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  white-space: nowrap;
  line-height: .5rem;
}

.button-with-icon-text img {
  margin: auto;
  width: auto;
  height: 75%;
  height: 50%;
}

.button-with-icon-text span {
  opacity: 0.75;
  font-size: 10px;
  letter-spacing: -.075em;
}

/* For App.jsx */

.header-container {
  position: fixed;
  z-index: 40;
  border-bottom-width: 1px;
  --border-opacity: 1;
  border-color: #f7fafc;
  border-color: rgba(247, 250, 252, var(--border-opacity));
  width: 100%;
  --bg-opacity: 1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  margin-left: auto;
  margin-right: auto;
  max-width: 480px;
  height: 4rem;
}

.header-item-group {
  display: block;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  height: 4rem;
  padding: 0.5rem;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  scrollbar-width: none; /* For Firefox */
}

/* For other browsers */

.header-item-group::-webkit-scrollbar {
  display: none;
}

.header-item {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.group-left {
  flex: 1 1 0%;
}

.group-left .header-item {
  margin-right: 0.25rem;
}

.group-left .header-item:last-child {
  margin-right: 0;
}

.group-right {
  flex: none;
}

.app-title {
  display: inline-block;
  height: 3rem;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-size: 1.25rem;
  font-weight: 700;
}

.main-container {
  padding-top: 4rem;
}

.main {
  margin-left: auto;
  margin-right: auto;
  max-width: 480px;
}

.main-additional {
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 2rem;
  margin-bottom: 4rem;
  line-height: 1;
}

.main-additional li {
  margin-bottom: 1rem;
}

.main-additional-button {
  border-radius: 0.5rem;
  width: 3rem;
  height: 3rem;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
}

.main-additional-button img {
  margin: auto;
  width: auto;
  height: 75%;
}

.counter {
  margin: 1rem;
  border-radius: 0.5rem;
  --bg-opacity: 1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, var(--bg-opacity));
  padding: 0.5rem;
  padding-left: 0.75rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.counter-checked {
  --bg-opacity: 1;
  background-color: #90cdf4;
  background-color: rgba(144, 205, 244, var(--bg-opacity));
}

.counter-row {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.counter-row .button-with-icon {
  margin-left: 0.25rem;
}

.counter-element-fill {
  flex: 1 1 0%;
}

.counter-label {
  flex: 1 1 0%;
  max-width: 100%;
  overflow-wrap: break-word;
  line-height: 1.25;
  font-size: 1.125rem;
  font-weight: 700;
}

.counter-value {
  flex: 1 1 0%;
  align-self: center;
  width: 0;
  font-size: 1.5rem;
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

/* For modals */

.modal-overlay {
  position: fixed;
  z-index: 50;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  --bg-opacity: 1;
  background-color: #000;
  background-color: rgba(0, 0, 0, var(--bg-opacity));
  --bg-opacity: 0.75;
}

.modal {
  position: absolute;
  right: 2rem;
  left: 2rem;
  top: 3rem;
  bottom: 3rem;
  outline: 0;
  margin-left: auto;
  margin-right: auto;
  border-width: 1px;
  --border-opacity: 1;
  border-color: #cbd5e0;
  border-color: rgba(203, 213, 224, var(--border-opacity));
  border-radius: 0.25rem;
  max-width: 480px;
  padding: 1.25rem;
  --bg-opacity: 1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, var(--bg-opacity));
  overflow: auto;
}

.modal h1 {
  margin-bottom: 0.5rem;
  font-size: 1.125rem;
  font-weight: 700;
}

.modal h2 {
  margin-bottom: 0.5rem;
  font-size: 1.125rem;
}

.modal li {
  display: flex;
  flex-direction: column;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.modal li > * {
  flex: 1 1 0%;
}

.modal label {
  font-weight: 700;
}

.modal .modal-counter-names {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-right: -0.25rem;
  line-height: 1.25;
}

.modal .modal-counter-names li {
  display: block;
  margin-left: 0;
  margin-right: 0.25rem;
  margin-top: 0;
  margin-bottom: 0.25rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  border-radius: 0.25rem;
  --bg-opacity: 1;
  background-color: #edf2f7;
  background-color: rgba(237, 242, 247, var(--bg-opacity));
  font-size: 0.875rem;
  --text-opacity: 1;
  color: #2d3748;
  color: rgba(45, 55, 72, var(--text-opacity));
}

.modal input {
  border-width: 1px;
  --border-opacity: 1;
  border-color: #a0aec0;
  border-color: rgba(160, 174, 192, var(--border-opacity));
  border-radius: 0.25rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
}

.modal input[type="number"] {
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

.modal input:invalid {
  --border-opacity: 1;
  border-color: #e53e3e;
  border-color: rgba(229, 62, 62, var(--border-opacity));
  --bg-opacity: 1;
  background-color: #fff5f5;
  background-color: rgba(255, 245, 245, var(--bg-opacity));
  --text-opacity: 1;
  color: #e53e3e;
  color: rgba(229, 62, 62, var(--text-opacity));
  font-weight: 700;
}

.modal .modal-input-constraint {
  padding-left: 0.25rem;
  padding-right: 0.25rem;
  line-height: 1.25;
  font-size: 0.75rem;
  --text-opacity: 1;
  color: #4a5568;
  color: rgba(74, 85, 104, var(--text-opacity));
  letter-spacing: -0.025em;
}

.modal .language-list {
  height: 3rem;
}

.modal .language-list .language-button {
  border-radius: 0.5rem;
  width: 3rem;
  height: 3rem;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  white-space: nowrap;
  width: auto;
  font-size: 1.25rem;
}

.modal hr {
  margin-top: 1.25rem;
  margin-bottom: 0.75rem;
}

.modal .modal-actions {
  display: flex;
  flex-direction: row;
}

.modal .modal-actions li {
  flex: 1 1 0%;
  height: 2.5rem;
}

.modal .action-button {
  border-radius: 0.5rem;
  width: 3rem;
  height: 3rem;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  white-space: nowrap;
  width: auto;
  margin-left: 0.25rem;
  margin-right: 0.25rem;
}

.modal .action-button img {
  margin: auto;
  width: auto;
  height: 75%;
}

/* Input with Buttons */

.extended-input {
  display: flex;
  flex-direction: row;
}

.extended-input input {
  flex: 1 1 0%;
  width: 0;
}

.extended-input button {
  border-radius: 0.5rem;
  width: 3rem;
  height: 3rem;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  white-space: nowrap;
  margin-left: 0.25rem;
  border-radius: 0.25rem;
  height: 2rem;
}

.extended-input button img {
  margin: auto;
  width: auto;
  height: 75%;
}

/* Hidden */

.force-hidden {
  display: none;
}

/* Button Themes */

.button-positive {
  --bg-opacity: 1;
  background-color: #c6f6d5;
  background-color: rgba(198, 246, 213, var(--bg-opacity));
}

.button-negative {
  --bg-opacity: 1;
  background-color: #fed7d7;
  background-color: rgba(254, 215, 215, var(--bg-opacity));
}

.modal .button-positive {
  --bg-opacity: 1;
  background-color: #c6f6d5;
  background-color: rgba(198, 246, 213, var(--bg-opacity));
}

.modal .button-negative {
  --bg-opacity: 1;
  background-color: #fed7d7;
  background-color: rgba(254, 215, 215, var(--bg-opacity));
}

button:disabled {
  opacity: 0.25;
}
